<template>
  <div id="ContactView">
    <div id="Header">
      <h1> My userlist list </h1>
    </div>

    <div v-if="isErrorMessage" style="color:red;">
        {{errorMessage}}
    </div>

    <div id="Content">
        <div v-if="isEditUser" id="modal">
          
          <div id="modalContent">
            <!-- In case of editing user -->
            <div v-if="selectedUser && selectedUser.name">
              Name : <input type="text" v-model="selectedUser.name" readonly> <br>
              Age : <input type="number" v-model="selectedUser.age"> <br>
              Company : <input type="text" v-model="selectedUser.company"> <br>
              Location : <input type="text" v-model="selectedUser.location"> <br>
            </div>

            <!-- In case of creating new user -->
            <div v-else>
              Name : <input type="text" v-model="userName"> <br>
              Age : <input type="number" v-model="userAge"> <br>
              Company : <input type="text" v-model="userCompany"> <br>
              Location : <input type="text" v-model="userLocation"> <br>
            </div>
          </div>
          
          <!-- Modal actions -->
          <div id="modalAction">
            <button @click="triggerUserAction()"> Save </button>
            <button @click="closeCreateModal()"> Close </button>
          </div>
        </div>

        <div v-else id="modal">
          <div id="commonAction">
            <img @click="openCreateModal()" id="icon" :src="addIcon" alt=" new user" title="Create new user">
          </div>

          <!-- List that is displaying all the users -->
          <div id="userList">
            <div id="card" v-for="user in users" :key="user._id">
              <div id="cardContent">
                <div id="userDetail">
                  <div>
                    Name : {{user.name}}
                  </div>
                  <div>
                    Age: {{user.age}}
                  </div>
                  <div>
                    Company: {{user.company}}
                  </div>
                  <div>
                    Location : {{user.location}}
                  </div>
                </div>
                <div id="userAction">
                  <img id="icon" @click="getUserDetail(user.name)" :src="editIcon" alt="edit user" title="edit">
                  <img id="icon" @click="deleteuserDetail(user)" :src="deleteIcon" alt="delete user" title="delete">
                </div>
              </div>
            </div>
          </div>
        </div>

    </div>
  </div>
</template>

<script>
import impAddIcon from '../assets/add.png'
import impEditIcon from '../assets/edit.png'
import impDeleteIcon from '../assets/delete.png'

export default {
  name: 'ContactView',
  data(){
    return{
      baseURL : "/v1",
      users : [],
      selectedUser : null,
      isEditUser : false,
      userName : '',
      userAge : 0,
      userCompany : '',
      userLocation : '',
      isErrorMessage : false,
      errorMessage : '',

      // Icons
      addIcon : impAddIcon,
      editIcon : impEditIcon,
      deleteIcon : impDeleteIcon
    }
  },
  methods:{
    // Fetched list for all users
    getAllUsers(){
      var self = this
      axios.get(this.baseURL + "/users").then(response => {
        this.users = response.data
			}).catch(function(err){
        self.displayError(err.response.data.message)
				console.error(err);
			})
    },

    // Fetches user detail
    getUserDetail(name){
      var self = this
      this.selectedUser = null
      axios.get(this.baseURL + "/user/" + name).then(response => {
        this.selectedUser = response.data
        this.openCreateModal()
			}).catch(function(err){
        self.displayError(err.response.data.message)
				console.error(err);
			})
    },

    // Creates a new user
    createNewUser(){
      var self = this
      var payload = {
        name : this.userName,
        age : parseInt(this.userAge),
        company : this.userCompany,
        location : this.userLocation,
      }

      axios.post(this.baseURL + "/user/new" , payload).then(response => {
        this.closeCreateModal()
        this.getAllUsers()
			}).catch(function(err){
        self.displayError(err.response.data.message)
				console.error(err);
			})
    },

    // Update the already existing user
    editUserDetail(){
      var self = this
      this.selectedUser.age = parseInt(this.selectedUser.age)
      axios.put(this.baseURL + "/user/" + this.selectedUser.name , this.selectedUser).then(response => {
        this.closeCreateModal()
        this.getAllUsers()
			}).catch(function(err){
        self.displayError(err.response.data.message)
				console.error(err);
			})
    },

    // Delete the existing user
    deleteuserDetail(user){
      var self = this
      var confirmation = confirm("Are you sure you want to delete " + user.name + " ?" )
      if(confirmation){
        axios.delete(this.baseURL + "/user/" + user.name).then(response => {
          this.getAllUsers()
        }).catch(function(err){
          self.displayError(err.response.data.message)
          console.error(err);
        })
      }
    },

    // Opens the pop-up model
    openCreateModal(){
      this.userAge = 0
      this.isEditUser = true
    },

    // Close the pop-up modal
    closeCreateModal(){
      this.isEditUser = false
      this.selectedUser = null
      this.userName = ''
      this.userEmail = ''
      this.userPhone = ''
      this.userCompany = ''
      this.userLocation = ''
    },

    displayError(message){
      this.isErrorMessage = true
      this.errorMessage = message
      this.hideErrorMessage()
    },

    hideErrorMessage(){
      var self = this
      setInterval(function(){ 
        self.isErrorMessage = false
        self.errorMessage = ''
      }, 5000);
    },

    // Triggers user action
    triggerUserAction(){
      if(this.selectedUser){
        this.editUserDetail()
      }
      else{
        if(this.userName)
          this.createNewUser()
        else
          alert("Name required")
      }
    }

  },
  created(){
    this.getAllUsers()
  }
}
</script>

<style scoped>
#ContactView{
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border:1px solid orange;
}
#Header{
  display: flex;
  justify-content: center;
  width : 300px;  
}
#Content{
  border : 1px solid;
  width : 40%;
  height : 80%;
}
#commonAction{
  height : 8%;
  margin-bottom: 10px;
}
#userList{
  height : 90%;
  overflow: auto;
}
#card{
  height: 60px;
  border-bottom : 1px dotted silver;
  padding : 10px;
}
#cardContent{
  display: flex;
  overflow: hidden;
  width: 100%;
}
#userDetail{
  width : 90%;
  margin-right: 5px;
  font-size : 12px;
}
#userAction{
  width : 10%;
  margin-left:5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#icon{
  height : 16px;
}
#modal{
  height : 100%;
}
</style>



